"use client";

import { BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer } from "recharts";

export default function SkillBarChart({ students }: { students: any[] }) {
  if (!students || students.length === 0) return null;

  // Avg skill values vs score
  const data = ["comprehension", "attention", "focus", "retention"].map((skill) => {
    const avgSkill = students.reduce((sum, s) => sum + (s[skill] || 0), 0) / students.length;
    const avgScore = students.reduce((sum, s) => sum + (s.assessment_score || 0), 0) / students.length;
    return { skill, value: avgSkill.toFixed(2), score: avgScore.toFixed(2) };
  });

  return (
    <div className="h-80">
      <h2 className="text-xl font-semibold mb-2">Skills vs Score</h2>
      <ResponsiveContainer width="100%" height="100%">
        <BarChart data={data}>
          <XAxis dataKey="skill" />
          <YAxis />
          <Tooltip />
          <Bar dataKey="value" fill="#3b82f6" name="Skill Avg" />
          <Bar dataKey="score" fill="#10b981" name="Score Avg" />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
}
