"use client";

import { ScatterChart, Scatter, XAxis, YAxis, Tooltip, ResponsiveContainer } from "recharts";

export default function AttentionScatter({ students }: { students: any[] }) {
  if (!students || students.length === 0) return null;

  const data = students.map((s) => ({
    attention: s.attention,
    score: s.assessment_score,
    name: s.name,
  }));

  return (
    <div className="h-80">
      <h2 className="text-xl font-semibold mb-2">Attention vs Score</h2>
      <ResponsiveContainer width="100%" height="100%">
        <ScatterChart>
          <XAxis dataKey="attention" name="Attention" />
          <YAxis dataKey="score" name="Score" />
          <Tooltip cursor={{ strokeDasharray: "3 3" }} />
          <Scatter data={data} fill="#f59e0b" />
        </ScatterChart>
      </ResponsiveContainer>
    </div>
  );
}
